import React from 'react';
import { NavLink } from 'react-router-dom';

import './NavItem.css';

const navigationItem = (props) => (
    <NavLink
      activeClassName="active"
      exact={props.exact}
      to={props.link}>
      {props.children}
    </NavLink>
);

export default navigationItem;


