import React, { Component } from 'react';

import Hoc from '../hoc/Hoc';
import Navbar from '../components/navbar/Navbar';

class Layout extends Component {
  render() {
    return (
      <Hoc>
        <Navbar />
        <main>
          {this.props.children}
        </main>
      </Hoc>
    )
  }
}

export default Layout;



