import React, { Component } from 'react';
import ReactTable from 'react-table';
import 'react-table/react-table.css';

import './DataTable.css';
import Hoc from '../../hoc/Hoc';

class DataTable extends Component {

  state = {
    data: [],
    columns: [],
    loading: true
  }

  updateSexColumn(columns, sex_data) {
    console.log(sex_data[0]);
    columns[9].filterMethod = (filter, row) => {
      if (filter.value === "all") {
        return true;
      }
      if (filter.value === sex_data[0]) {
        return row[filter.id] === sex_data[0];
      }
      return row[filter.id] === sex_data[1];
    },
    columns[9].Filter= ({ filter, onChange }) => 
    <select
      onChange={event => onChange(event.target.value)}
      style={{ width: "100%" }}
      value={filter ? filter.value : "all"}
    >
      <option value="all">Show All</option>
      <option value={sex_data[0]}>{sex_data[0]}</option>
      <option value={sex_data[1]}>{sex_data[1]}</option>
    </select>
    return columns;
  }

  updateRelationColumn(columns, relationship_data) {
    columns[7].filterMethod = (filter, row) => {
      if (filter.value === "all") {
        return true;
      }
      if (filter.value === relationship_data[0]) {
        return row[filter.id] === relationship_data[0];
      }
      if (filter.value === relationship_data[1]) {
        return row[filter.id] === relationship_data[1];
      }
      if (filter.value === relationship_data[2]) {
        return row[filter.id] === relationship_data[2];
      }
      if (filter.value === relationship_data[3]) {
        return row[filter.id] === relationship_data[3];
      }
      if (filter.value === relationship_data[4]) {
        return row[filter.id] === relationship_data[4];
      }
      return row[filter.id] === relationship_data[5];
    },
    columns[7].Filter= ({ filter, onChange }) => 
    <select
      onChange={event => onChange(event.target.value)}
      style={{ width: "100%" }}
      value={filter ? filter.value : "all"}
    >
      <option value="all">Show All</option>
      <option value={relationship_data[0]}>{relationship_data[0]}</option>
      <option value={relationship_data[1]}>{relationship_data[1]}</option>
      <option value={relationship_data[2]}>{relationship_data[2]}</option>
      <option value={relationship_data[3]}>{relationship_data[3]}</option>
      <option value={relationship_data[4]}>{relationship_data[4]}</option>
      <option value={relationship_data[5]}>{relationship_data[5]}</option>
    </select>
    return columns;
  }

  updateRaceColumn(columns, race_data) {
    columns[8].filterMethod = (filter, row) => {
      if (filter.value === "all") {
        return true;
      }
      if (filter.value === race_data[0]) {
        return row[filter.id] === race_data[0];
      }
      if (filter.value === race_data[1]) {
        return row[filter.id] === race_data[1];
      }
      if (filter.value === race_data[2]) {
        return row[filter.id] === race_data[2];
      }
      if (filter.value === race_data[3]) {
        return row[filter.id] === race_data[3];
      }
      return row[filter.id] === race_data[4];
    },
    columns[8].Filter= ({ filter, onChange }) => 
    <select
      onChange={event => onChange(event.target.value)}
      style={{ width: "100%" }}
      value={filter ? filter.value : "all"}
    >
      <option value="all">Show All</option>
      <option value={race_data[0]}>{race_data[0]}</option>
      <option value={race_data[1]}>{race_data[1]}</option>
      <option value={race_data[2]}>{race_data[2]}</option>
      <option value={race_data[3]}>{race_data[3]}</option>
      <option value={race_data[4]}>{race_data[4]}</option>
    </select>
    return columns;
  }

  updateColumns(columns, sex_data, relationship_data, race_data) {
    columns = this.updateRelationColumn(columns, relationship_data);
    columns = this.updateSexColumn(columns, sex_data);
    columns = this.updateRaceColumn(columns, race_data);
    console.log(columns);
    return columns
  }

  componentDidMount () {
    fetch("http://13.127.129.1:8081/api/get_user_data")
      .then(res => res.json())
      .then(
        (result) => {
          // console.log(result);
          const columns = this.updateColumns(result.columns, result.sex_data, result.relationship_data, result.race_data);
          console.log(columns)
          this.setState({
            data: result.data,
            columns: columns,
            loading: false
          });
        },
        (error) => {
          console.log(error);
        }
      )
  }
  render() {
    let dom = this.state.loading ? 
    (
      <div>Loading userdata...</div>
    )
    :
    (
      <div className="row">
        <div className="col-md-12">
          <ReactTable
            data={this.state.data}
            filterable
            defaultFilterMethod={(filter, row) => 
              true
            }
            columns={this.state.columns}
          />
        </div>
      </div>
    )
      
    return (
      <Hoc>
        {dom}
      </Hoc>
    )

    }
}

export default DataTable;



