import React from 'react';

import './Navbar.css';
import NavItem from './navItem/NavItem';

const navbar = (props) => {
  return (
    <div className="topnav">
      <NavItem link="/" exact>Home</NavItem>
      <NavItem link="/data" exact>Data</NavItem>
    </div>
  )
}

export default navbar;


