import React, { Component } from 'react';

import { Pie } from 'react-chartjs-2';

const data = {
	labels: [
		'Female',
		'Male',
	],
	datasets: [{
		data: [0, 0],
		backgroundColor: [
		'#FF6384',
		'#36A2EB',
		],
		hoverBackgroundColor: [
		'#FF6384',
		'#36A2EB',
		]
	}]
};

const data2 = {
	labels: [
		'Husband',
    'Not-in-family',
    'Other-relative',
    'Own-child',
    'Unmarried',
    'Wife'
	],
	datasets: [{
		data: [0, 0, 0, 0, 0, 0],
		backgroundColor: [
		'#FF6384',
    '#36A2EB',
    '#b0e0e6',
    '#ffb6c1',
    '#0e2f44',
    '#660066',
		],
		hoverBackgroundColor: [
		'#FF6384',
    '#36A2EB',
    '#b0e0e6',
    '#ffb6c1',
    '#0e2f44',
    '#660066',
		]
	}]
};

class Dashboard extends Component {
  state = {
    maleCount: 0,
    femaleCount: 0
  }

  componentDidMount () {
    fetch("http://13.127.129.1:8081/api/get_sex_count")
    .then(res => res.json())
    .then(
      (result) => {
        // console.log(result.result2);
        data.datasets[0].data = [
          result.result1[data.labels[0]], 
          result.result1[data.labels[1]]
        ]
        data2.datasets[0].data = [
          result.result2[data2.labels[0]], 
          result.result2[data2.labels[1]],
          result.result2[data2.labels[2]],
          result.result2[data2.labels[3]],
          result.result2[data2.labels[4]],
          result.result2[data2.labels[5]]
        ]
        this.setState({maleCount: result.result1.male_count, femaleCount: result.result1.female_count})
      },
      // Note: it's important to handle errors here
      // instead of a catch() block so that we don't swallow
      // exceptions from actual bugs in components.
      (error) => {
        
      }
    )
  }

  render() {
    // data.datasets[0].data = [this.state.femaleCount, this.state.maleCount];
    return (
      <div className="App">
        <div className="row">
          <div className="col-md-6">
            <h2>Male and Female Count Chart</h2>
            <Pie
              data={data} 
              width={100}
              height={50}
            />
          </div>
          <div className="col-md-6">
            <h2>Relationship Chart</h2>
            <Pie
              data={data2} 
              width={100}
              height={50}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default Dashboard;




